/* Button style */

:root {
  --backgroundColor: #0f0026;
  --rouletteColor: white;
}

@-webkit-keyframes roulette {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes roulette {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#header button::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 30%;
  aspect-ratio: 1;
  background-image: radial-gradient(white 20%, transparent 21%), linear-gradient(transparent 45%, white 46% 54%, transparent 55%), linear-gradient(90deg, transparent 45%, white 46% 54%, transparent 55%);
  background-image: radial-gradient(var(--rouletteColor) 20%, transparent 21%), linear-gradient(transparent 45%, var(--rouletteColor) 46% 54%, transparent 55%), linear-gradient(90deg, transparent 45%, var(--rouletteColor) 46% 54%, transparent 55%);
  -webkit-animation: rouletteCenter 1s infinite linear;
  animation: rouletteCenter 1s infinite linear;
}

@-webkit-keyframes rouletteCenter {
  from {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
  }

  to {
    -webkit-transform: translate(-50%, -50%) rotate(-360deg);
            transform: translate(-50%, -50%) rotate(-360deg);
  }
}

@keyframes rouletteCenter {
  from {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
  }

  to {
    -webkit-transform: translate(-50%, -50%) rotate(-360deg);
            transform: translate(-50%, -50%) rotate(-360deg);
  }
}

#header button::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 10%;
  width: 10%;
  aspect-ratio: 1;
  border-radius: 50%;
  background-color: white;
  background-color: var(--rouletteColor);
  -webkit-transform-origin: 50% 450%;
          transform-origin: 50% 450%;
  -webkit-animation: rouletteBall 2s infinite linear;
  animation: rouletteBall 2s infinite linear;
}

@-webkit-keyframes rouletteBall {
  from {
    -webkit-transform: translate(-50%, -50%) rotate(0deg) translateY(0%);
            transform: translate(-50%, -50%) rotate(0deg) translateY(0%);
  }

  to {
    -webkit-transform: translate(-50%, -50%) rotate(360deg) translateY(0%);
            transform: translate(-50%, -50%) rotate(360deg) translateY(0%);
  }
}

@keyframes rouletteBall {
  from {
    -webkit-transform: translate(-50%, -50%) rotate(0deg) translateY(0%);
            transform: translate(-50%, -50%) rotate(0deg) translateY(0%);
  }

  to {
    -webkit-transform: translate(-50%, -50%) rotate(360deg) translateY(0%);
            transform: translate(-50%, -50%) rotate(360deg) translateY(0%);
  }
}

/* Header */
.text-container {
  display: flex;
  /*   border:1px solid red; */
  width: 100%;
  /* height: 100%; */
  align-items: center;
}

.text-container svg {
  display: block;
  font: normal 6.875em/1.25 'TTSquaresCondensed-Thin';
  /* font: normal 10.5em 'TTSquaresCondensed-Thin'; */
  width: 100%;
  margin: 0 auto;
}

.text-copy {
  fill: none;
  stroke: white;
  stroke-dasharray: 6% 15%;
  stroke-width: 5px;
  stroke-dashoffset: 0%;
  -webkit-animation: stroke-offset 5.5s infinite linear;
          animation: stroke-offset 5.5s infinite linear;
}

.text-copy:nth-child(1) {
  stroke: #2de0b6;
  -webkit-animation-delay: -1;
          animation-delay: -1;
}

.text-copy:nth-child(2) {
  stroke: #36bf9f;
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
}

.text-copy:nth-child(3) {
  stroke: #00ffc3;
  -webkit-animation-delay: -3s;
          animation-delay: -3s;
}

.text-copy:nth-child(4) {
  stroke: #3e9682;
  -webkit-animation-delay: -4s;
          animation-delay: -4s;
}

.text-copy:nth-child(5) {
  stroke: #00ffc3;
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
}


@-webkit-keyframes stroke-offset {
  100% {
    stroke-dashoffset: -35%;
  }
}


@keyframes stroke-offset {
  100% {
    stroke-dashoffset: -35%;
  }
}

@media only screen and (max-width: 600px) {
  .text-container svg {
    font: normal 2.7em/1.25 'TTSquaresCondensed-Thin';
    height: 80px;
  }

  .text-copy {
    stroke-dasharray: 6% 10%;
    stroke-width: 1px;
  }

  .ui-grid .ui-box #description .container .text-box h2 {
    font-size: 18px;
  }

  .ui-grid .ui-box #description .container .text-box h3 {
    font-size: 16px;
  }

  .ui-grid .ui-box #description .container .text-box p {
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .ui-grid {
    grid-template-columns: 1% 98% 1%;
    grid-template-rows: 15% 70% 15%;
  }

  .ui-c-border {
    display: none;
  }

  .ui-border {
    display: none;
  }

  .ui-circles {
    display: none;
  }

  .ui-border-v2.t {
    width: 100%;
    height: 30px;
  }
}

/*  Button */

* {
  list-style: none;
  text-decoration: none;
  outline: 0;
}
a {
  color: #E91E63;
  text-indent: -10px;
  font-size: 18px;
}
a:active, a:focus {
  color: #D81B60;
  font-weight: bold;
}
label, label:before, label:after {
  position: absolute;
  bottom: 50px;
  right: 10%;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #E91E63;
  box-shadow: rgba(0, 0, 0, 0.117647) 0px 0px 8px 0px, 
              rgba(0, 0, 0, 0.239216) 0px 8px 8px 0px;
  transition: 0.6s cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 2;
}
label #open {
  position: absolute;
  top: -22px;
  left: 13px;
  color: white;
  font-size: 35px!important;
  cursor: pointer;
}
label #close {
  opacity: 0;
  cursor: pointer;
}
#menu-toggle:not(:checked) + label:hover {
  box-shadow: rgba(0, 0, 0, 0.0784314) 0px 0px 12px 0px, 
              rgba(0, 0, 0, 0.239216) 0px 12px 12px 0px;
}

#menu {
  position: absolute;
  padding: 0;
  bottom: 199px;
  right: 13%;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: none;
  transition: 0.6s cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 1;
}

#menu-toggle:checked + label {
  position: absolute;
  bottom: 30%;
  right: 30%;
  width: 200px;
  height: 310px;
  background-color: white;
  border-radius: 3px;
  box-shadow: rgba(0, 0, 0, 0.117647) 0px 0px 8px 0px, 
              rgba(0, 0, 0, 0.239216) 0px 8px 8px 0px;
  transition: 0.6s cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 1;
}
#menu-toggle:checked + label #open {
  opacity: 0;
  pointer-events: none;
}
#menu-toggle:checked + label #close {
  position: absolute;
  top: -20px;
  right: 20px;
  color: #E91E63;
  font-size: 30px!important;
  opacity: 1;
}

#menu-toggle:checked + label + #menu {
  position: absolute;
  margin: 0;
  bottom: 30%;
  right: 30%;
  width: 200px;
  height: 250px;
  border-radius: 3px;
  transition: 0.6s cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 2;
}

#menu-toggle:not(:checked) + label + #menu li {
  opacity: 0;
  position: absolute;
  top: 0; left: 0;
  pointer-events: none;
  
}
#menu-toggle:checked + label + #menu li {
  opacity: 1;
  margin-left: 30px;
  padding-bottom: 10px;
  transition: .2s ease .1s;
}
[type="checkbox"]:not(:checked), [type="checkbox"]:checked  {
  display: none;
}
